a {
    color: inherit;
}

h1 {
    text-decoration: none;
    color: inherit;
}


div.body {
    background: #4d4a40;
    height: 1000px;
    padding: 20px 0;

    line-height: 2;

    min-width: 1000px;
}

.article-container {
    background: #fff;
    width: 90%;
    margin: 0 auto;
    padding: 30px 0;
}

.article-container header {
    background: #267890;
    color: #fff;
    border: 5px solid #144141;
    margin: 0 -34px;
}

.article-container header h1 {
    font-size: 42px;
    font-weight: bold;
}

.article-container header .origin-link {
    color: #dbdbdb;
    margin-bottom: 30px;
    line-height: 1px;
}

.article-container header .origin-link a {
    text-decoration: underline;
}

.article-container header .origin-link a:hover {
    color: #fff;
}

.article-container section {
    width: 90%;
    margin: 0 auto;
}

.article-container section p {
    margin: 1em;
}

.article-container section h2 {
    font-size: 32px;
    font-weight: bold;
    border-top: 1px dotted;
    border-bottom: 1px dotted;
}